:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 650ms;
}
body {
  background-color: gainsboro;
}
.gallery {
  display: grid;
  width: 1200px;
  grid-template-columns: repeat(auto-fill, minmax(370px, 1fr));
  grid-gap: 32px;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
}

.gallery-item {
  position: relative;
  height: 265px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.photo-card {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.photo-card:hover {
  transform: scale(1.06);
  transition: transform var(--animation-duration) var(--timing-function);
}
.gallery-link {
  display: block;
  text-decoration: none;
  height: 100%;
}

.gallery-link:hover {
  cursor: zoom-in;
}
.Btn {
  display: flex;
  background-color: dodgerblue;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}
.Btn.is-hidden {
  opacity: 0;
}
.stats {
  display: flex;
  background-color: gold;
}
.stats-item {
  display: flex;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 30px;
  justify-content: flex-start;
}
